import React from 'react';
import Carousel from './Carousel';

function ChatPals({ pals }) {
  return (
    <>
      <Carousel count={5} chat={true} users={[{name: 'Sarah'}, {name: 'Katie'}, {name: 'Don'}, {name: 'Lilah'}, {name: 'Guadalupe'}, {name: 'etf'}]} />
    </>
  )
}

export default ChatPals;